.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  max-width: 100%;
  overflow-x: hidden;
  background-color: #dfebf6;
}

.header {
  margin-right: 5%;
  margin-left: 5%;
  user-select: none;
  width: 100%;
  height: 100px;
  display:table;
}

.separator {
  width: 100%;
  height: 100px;
  display:table;
  background-color: #bfdef2;
}

.footer {
  display: table;
  width: 100%;
  height: 150px;
}

h1{
  color: #32292f;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  font-family: 'Montserrat';
  font-size: 32px;
  font-weight: bolder;
}

.graph {
  height: 10%;
  width: 100%;
  margin: 0 auto;
}
